<template>

    <div
        v-if="currentOrder"
        class="received-loan-container">

        <div class="go-back-link">
            <router-link to="/orders"><span class="icon-arrow-left"></span>Назад</router-link>
        </div>

        <div class="page-title">
            <h1>Кредит «{{currentOrder.name}}»</h1>
        </div>

        <div class="block">

            <div class="block-title">
                <div class="h2">Информация о кредите</div>
            </div>

            <div class="block-content">

                <div class="received-load-info">
                    <ul>
                        <li>
                            <span>Сумма кредита</span>
                            <span>{{parsePay(currentOrder.sum)}} <span class="icon-ruble"></span></span>
                        </li>
                        <li>
                            <span>Срок кредита</span>
                            <span>{{currentOrder.term}} мес.</span>
                        </li>
                        <li>
                            <span>Процентная ставка</span>
                            <span v-if="currentTariff">{{currentOrder.insurance ? currentTariff.rateWithInsurance : currentTariff.rateWithoutInsurance }} %</span>
                        </li>
                        <li>
                            <span>Дата создания</span>
                            <span>{{getDate()}}</span>
                        </li>
                    </ul>
                </div>

            </div>
        </div>

        <div class="block">
            <div class="block-title">
                <div class="h2">Документы</div>
            </div>
            <div class="block-content">
                <div class="documents-list documents-list-with-links">
                    <ul>
                        <li v-for="doc in currentOrderDocs"
                            :key="doc.id">

                            <div class="document-item">
                                <div class="document-item-title">
                                    {{doc.name}}
                                </div>
                                <div class="document-files-list">
                                    <div v-for="file in doc.files"
                                         :key="file.id"
                                         class="files-item">
                                        <span class="document-link"
                                              @click="openDocViewModal(file)">
                                            <span class="icon-document"></span>
                                            <span class="document-link-main">
                                                <span class="document-link-title">{{file.name}}.{{file.format}}</span>
                                                <span class="document-link-size">({{convertSize(file.size)}})</span>
                                            </span>
                                        </span>
                                    </div>
                                </div>
                            </div>

                            <div class="document-download-link">
                                <a
                                    href="#"
                                    class="download-link"
                                    @click.prevent="downloadFile(doc)">Скачать</a>
                            </div>

                        </li>
                    </ul>
                </div>
            </div>
        </div>

        <DocView v-if="viewDocumentModal"
                 :list="viewDocument"
                 @close-modal="viewDocumentModal = false"
                 :familiarizedBtn="false"/>

        <Popup v-if="docViewErrorModal"
               :error="true"
               @close-modal="docViewErrorModal = false">

            <div class="popup-title">
                <div class="h3">Документы формата DOC, DOCX, XLS и XLSX нельзя просматривать через браузер</div>
            </div>

            <div class="popup-body">
                <p>Для просмотра данного документа Вам необходимо его скачать</p>
                <button
                    class="btn btn-lg btn-block close-modal"
                    @click.prevent="downloadFile(downloadedDoc)">Скачать</button>
            </div>

        </Popup>

    </div>

</template>

<script>

    import moment from 'moment'
    import { mapState } from 'vuex'
    import DocView from "@/components/Blocks/DocView"
    import Popup from "@/components/Blocks/Popup"

    export default {
        name: "ReceivedLoad",
        components: {
            DocView,
            Popup
        },
        data: () => ({
            receivedLoan: {
                id: 1,
                title: 'Быстрый!',
                startDate: '01.01.2021',
                endDate: '01.01.2024',
                sum: 38000000,
                percent: '6.6%',
                paidOut: 17000000
            },
            viewDocumentModal: false,
            docViewErrorModal: false,
            viewDocument: [],
            downloadedDoc: ''
        }),
        computed: {
            ...mapState({
                tariffs: state => state.tariffs.tariffs,
                currentOrder: state => state.createOrder.orderFullInfo.order,
                currentOrderDocs: state => state.orders.orderDocs
            }),
            currentTariff () {
                return this.tariffs.filter(tariff => tariff.id === this.currentOrder.tariff_id)[0]
            }
        },
        methods: {
            parsePay(number) {
                return number.toString().replace(/(\d)(?=(\d{3})+(\D|$))/g, '$1 ')
            },
            getDate () {
                const res = moment(this.currentOrder.created_at).format('DD.MM.YYYY')
                return res
            },
            openDocViewModal(doc) {
                if (doc.format === 'pdf' || doc.format === 'jpg' || doc.format === 'png') {
                    this.viewDocument = doc
                    this.viewDocumentModal = true
                } else {
                    this.downloadedDoc = doc
                    this.docViewErrorModal = true
                }
            },
            convertSize(size) {
                if ( size > 999 && size < 1000000) {
                    return `${(size / 1000).toFixed(2)}КБ`
                }
                else if ( size > 999999 && 1000000000 ) {
                    return `${(size / 1000000).toFixed(2)}МБ`
                }
                else {
                    return `${size.toFixed(2)}Б`
                }
            },
            downloadFile(doc) {
                if (doc.files) {
                    doc.files.forEach(file => {
                        this.$store.dispatch('getFile', file.id).then(content => {
                            this.$store.dispatch('downloadFile', {content, name: file.name, format: file.format})
                        })
                    })
                } else {
                    this.$store.dispatch('getFile', doc.id).then(blob => {
                        this.$store.dispatch('downloadFile', {content: blob, name: doc.name, format: doc.format})
                    })
                }
            }
        },
        async mounted () {
            const id = parseInt(this.$route.params.id)
            await this.$store.dispatch('getCurrentOrder', id)
            if (this.tariffs.length === 0) {
                await this.$store.dispatch('getTariffs')
            }
            await this.$store.dispatch('getDetailOrderDocs', id)
        }
    }
</script>
